#overlay {
	display: block;
	position: fixed;
	z-index: 1;
	left: 0;
	top: 0;
	width: 100%;
	height: 100%;
	overflow: auto;
	background-color: rgb(0, 0, 0);
	background-color: rgba(0, 0, 0, 0.4);
}

#modal {
	position: relative;
	top: 10%;
	background-color: transparent;
	margin: 0 auto;
	width: fit-content;
	max-width: 90%;
	padding-bottom: 30rem;
	border-radius: 0.4rem;
	/* border: 1px solid #888; */
}

.close {
	color: black;
	font-weight: bold;
	position: absolute;
	right: 0.7%;
	top: 0%;
}

.close:hover,
.close:focus {
	color: grey;
	text-decoration: none;
	cursor: pointer;
}
